body,
body * {
  margin: 0;
  padding: 0;
  vertical-align: top;
  box-sizing: border-box;
}

body {
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}
.container {
  width: 70%;
  margin: auto;
  padding-top: 20px;
}

.form-container {
  display: flex;
  max-height: 420px;
}

.img {
  width: 100%;
  background: url('../public/img/Envy-Colt.png') center/contain no-repeat;
}
.form {
  font-style: italic;
  gap: 15px 0;
  padding: 16px;
  background-color: #5a5f6f;
  color: #fff;
  border-radius: 3px;
}

input,
select {
  width: 100%;
  border: none;
  outline: none;
  background-color: #5a5f6f;
  border-bottom: 2px solid #fff;
  color: #fff;
}
form h4 {
  padding: 8px;
  line-height: 30px;
}
form input {
  margin: 7px 0;
  padding: 7px;
}

form input.ride {
  display: block;
  align-self: flex-start;
  width: 30%;
}

.heading,
.flex-card h4,
.sorting,
.form h3 {
  font-family: cursive;
  padding: 5px 10px 20px 10px;
}
.heading {
  padding: 0;
}
.field {
  width: 65%;
  display: flex;
  align-items: center;
}
.isAvailable,
.isBusy {
  background-color: #5db75f;
  width: fit-content;
  text-align: center;
  align-items: center;
  line-height: 7px;
  padding: 5px 10px;
  margin: 7px;
  border: 2px solid #fff;
  border-radius: 5px 15px;
}
p.isAvailable,
p.isBusy {
  height: 30px;
}
.isBusy {
  background-color: #d45449;
  border-radius: 15px 5px;
}
.flex-card {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  text-align: center;
  gap: 15px 0;
  padding: 16px;
  background-color: #5a5f6f;
  color: #fff;
  border-radius: 3px;
  margin: 20px 0;
}
.flex-row {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.flex-card p,
.flex-card h4,
.sorting {
  font-size: 16px;
  width: 16%;
  padding: 10px;
  border-bottom: 1px solid #fff;
}
.sorting {
  text-overflow: ellipsis;
  font-weight: bold;
  margin: 7px 3px;
  border: 3px solid #777c8a;
  border-radius: 5px;
  padding: 0;
}
.sorting:hover {
  background-color: #777c8a;
}
.sorting .btns {
  width: 10%;
}

.btns {
  width: 17%;
  display: flex;
  flex-direction: column;
}
button {
  border: none;
  border-radius: 5px;
  background-color: #5db75f;
  color: #fff;
  padding: 7px;
  margin: 8px;
  cursor: pointer;
  justify-items: flex-end;
}
button.dlt {
  background-color: #d45449;
}
button.put {
  background-color: #44cceb;
  padding: 6px 15px;
  float: right;
}
.modal-layer {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #0a0a0ae5;
}
.modal {
  border: 3px solid #4e8d4e;
  position: relative;
  display: flex;
  width: 500px;
  background-color: #5db75f;
  align-items: center;
  border-radius: 5px;
}
.right-side.form {
  font-style: italic;
  gap: 15px 0;
  background-color: #5a5f6f;
  border-radius: 0;
  color: #fff;
}
.modal .right-side {
  width: 55%;
}
.modal .left-side {
  text-align: center;
  font-family: cursive;
  width: 45%;
}
.left-side h4 {
  margin: 20px;
}

fieldset {
  padding: 0 0 10px 10px;
}

.statistic {
  display: flex;
  font-family: monospace;
  font-size: 20px;
  margin: 30px 0;
}
.statistic p {
  width: 50%;
  padding: 0 20px;
  border-left: 2px solid #fff;
}

.close-x {
  padding: 5px 8px;
  position: absolute;
  top: 0;
  right: 0;
}
.old {
  background-color: #d45449;
  padding: 5px;
  border-radius: 5px;
  line-height: 30px;
}
img.scooter {
  width: 50%;
}

@media screen and (max-width: 700px) {
  .btns {
    display: flex;
    flex-direction: row;
    width: 60%;
  }
  .btns button {
    width: 100%;
  }
}
